import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		uniNavBar,
		
	},
	data() {
		return {
			phone:"",
			detailData:{}
		}
	},
	onLoad(options) {
		this.detailData = JSON.parse(options.data);
		console.log(this.detailData)
		this.phone=this.detailData.phone
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//保存
